<template>
  <div class="cleanwater">
    <!-- 淨水廠專區 -->
    <div class="row" style="margin:0 15px 0 15px">
      <div class="col-12 text-center">
        <span
          class="badge badge-pill badge-info animate__animated animate__fadeInUp text-center title"
          style="animation-duration: 1s;animation-iteration-count:1"
        >淨水廠區</span>
        <!-- 地圖  -->
        <el-row type="flex" align="middle" :gutter="30">
          <el-col :span="10">
            <l-map
              style="height: 400px; width:100%; margin: 0 auto;"
              :zoom="zoom"
              :options="options"
              :center="center"
            >
              <!-- 載入地圖資料 -->
              <l-tile-layer :url="url" :attribution="attribution" />
              <!-- 地標 -->
              <l-marker
                ref="location"
                :lat-lng="item.local"
                v-for="item in MapData"
                :key="item.name"
                @click="tableHighlight(item.name)"
              >
                <!-- 地標圖示 -->
                <l-icon :icon-url="iconimg" />
                <!-- 點擊圖標會彈出顯示的文字 -->
                <l-popup>{{ item.name }}</l-popup>
              </l-marker>
            </l-map>
          </el-col>

          <!-- 淨水廠資訊 -->
          <el-col :span="14">
            <el-collapse
              v-model="activeNames"
              @change="handleChange"
              class="collapseBlock"
              v-if="getDataFinished"
            >
              <el-collapse-item
                :name="index"
                v-for="(item,index) in cleanwaterData"
                :key="item.name"
              >
                <template slot="title">
                  {{ item.name }}
                  <div class="pass">是否通過檢驗 : {{ item.pass }}</div>
                </template>
                <el-table
                  :data="tableData[index]"
                  stripe
                  fit
                  style="padding: 0 20px; margin-bottom: 10px;"
                >
                  <el-table-column prop="type" label="種類"></el-table-column>
                  <el-table-column prop="dirty" label="濁度"></el-table-column>
                  <el-table-column prop="ph" label="PH值"></el-table-column>
                  <el-table-column prop="ceil" label="大腸桿菌值"></el-table-column>
                </el-table>
                <div style="margin-left: 70%;">更新時間 : {{ tableData[0][0].updatetime }}</div>
              </el-collapse-item>
            </el-collapse>
          </el-col>
        </el-row>
      </div>
    </div>
    <div class="footer"></div>
  </div>
</template>
<script>
import axios from "axios";

export default {
  mounted() {
    this.getcleanwater();
  },
  methods: {
    // 根據地圖上點哪地標，將對應資料移動到右側圖表最上面並展開
    tableHighlight(value) {
      console.log(value);
      this.cleanwaterData = [];
      this.tableData = [];
      this.cleanwaterData = this.originalData.filter((item) => {
        return item.name !== value;
      });
      let keyItem = this.originalData.filter((item) => {
        return item.name === value;
      });
      this.cleanwaterData.splice(0, 0, ...keyItem);
      console.log(this.cleanwaterData);
      this.cleanwaterData.forEach((item) => {
        let obj = [
          {
            type: "測值",
            dirty: item.dirty,
            ceil: item.ceil,
            ph: item.ph,
            updatetime: item.updatetime,
          },
          {
            type: "最大限值",
            dirty: this.cleanwaterStandeardData[0].dirtystandard,
            ceil: this.cleanwaterStandeardData[0].ceilstandard,
            ph: this.cleanwaterStandeardData[0].phstandard,
          },
        ];
        this.tableData.push(obj);
      });
    },
    async getcleanwater() {
      await axios
        .get("server/cleanwater2.json")
        .then((res) => {
          console.log(res.data);
          if (res.data.length) {
            this.originalData = res.data;
            this.cleanwaterData = this.originalData;
          }
          console.log(res);
          //   console.log(this.cleanwaterData);
        })
        .catch((err) => {
          console.log(err);
        });
      await axios
        .get("server/cleanwaterStandard.json")
        .then((res) => {
          if (res.data.length) {
            this.cleanwaterStandeardData = res.data;
          }
          console.log(this.cleanwaterStandeardData);
        })
        .catch((err) => {
          console.log(err);
        });
      this.cleanwaterData.forEach((item) => {
        let obj = [
          {
            type: "測值",
            dirty: item.dirty,
            ceil: item.ceil,
            ph: item.ph,
            updatetime: item.updatetime,
          },
          {
            type: "最大限值",
            dirty: this.cleanwaterStandeardData[0].dirtystandard,
            ceil: this.cleanwaterStandeardData[0].ceilstandard,
            ph: this.cleanwaterStandeardData[0].phstandard,
          },
        ];
        this.tableData.push(obj);
      });
      console.log(this.tableData);
      this.getDataFinished = true;
    },
    handleChange(val) {
      console.log(val);
    },
  },
  data() {
    return {
      getDataFinished: false,
      tableData: [],
      cleanwaterStandeardData: [],
      cleanwaterData: [],
      originalData: [],
      activeNames: [0],
      // ---------------------地圖資料---------------------
      //地圖縮放比例
      zoom: 9,
      //地圖中心
      center: [22.912786, 120.442959],
      // 地標圖示
      iconimg: require("leaflet/dist/images/marker-icon.png"),
      //地圖使用網址
      url: "https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png",
      //地圖來源聲明
      attribution: `© <a href="http://osm.org/copyright">OpenStreetMap</a> contributors`,
      // 取消顯示地圖左上角縮放地圖的按鈕
      options: {
        zoomControl: false,
      },
      // ---------------------地圖打點資料---------------------
      MapData: [
        {
          name: "寶來淨水場",
          local: [23.1075319, 120.700922],
        },
        {
          name: "手巾寮淨水場",
          local: [22.8825185, 120.4955405],
        },
        {
          name: "新威淨水場",
          local: [23.0026195, 120.6545022],
        },
        {
          name: "茂林淨水場",
          local: [22.887452, 120.662895],
        },
        {
          name: "美濃淨水場",
          local: [22.888106, 120.536352],
        },
        {
          name: "多納淨水場",
          local: [22.9104378, 120.7154531],
        },
        {
          name: "民生淨水場",
          local: [23.2669093, 120.7206086],
        },
        {
          name: "木梓淨水場",
          local: [23.0179331, 120.5534823],
        },
        {
          name: "六龜淨水場",
          local: [22.9928944, 120.6390033],
        },
        {
          name: "甲仙淨水場",
          local: [23.0870219, 120.589685],
        },
        {
          name: "拷潭淨水場",
          local: [22.5912988, 120.4000535],
        },
        {
          name: "坪頂淨水場",
          local: [22.6800953, 120.4084894],
        },
        {
          name: "鳳山淨水場",
          local: [22.5461349, 120.3910758],
        },
        {
          name: "澄清湖淨水場",
          local: [22.6519623, 120.3495696],
        },
      ],
    };
  },
};
</script>
<style lang="scss" scoped>
.cleanwater {
  padding: 2rem 4rem;
  background-color: #e3f0f7;
  font-family: 微軟正黑體;
  .footer {
    background-color: #e3f0f7;
    height: 100px;
  }
  .title {
    margin: 0 auto 3% auto;
    border-color: #dcdcdc;
    border-width: 2px;
    padding: 0.5% 0 0.5% 1%;
    display: inline-block;
    line-height: 60px;
    letter-spacing: 20px;
    text-decoration: none;
    font-weight: 600;
    outline: 0;
    font-size: 2.5em;
  }
  .collapseBlock {
    height: 350px;
    overflow-x: hidden;
    overflow-y: auto;
    border-radius: 10px;
    .pass {
      position: absolute;
      right: 50px;
    }
    /deep/ .el-collapse-item__header {
      position: relative;
      padding-left: 50px;
      font-size: 15px;
    }
    /deep/.cell {
      text-align: center;
    }
  }
  /deep/.el-table__row.el-table__row--striped {
    background-color: #ccff99;
  }
  @media (min-width: 576px) {
    #background {
      padding: 3rem 2rem;
    }
  }
  @media (max-width: 980px) {
    #h2o {
      font-size: 2.5em;
    }
  }
  @media (max-width: 380px) {
    #h2o {
      font-size: 1.5em;
    }
  }
}
</style>